import React, { useState, useEffect, useRef } from 'react';
import { Row, Col, Card, Statistic, Progress, Spin, App, Button, InputNumber, Alert, Tag, theme } from 'antd';
import { Line, Column, Gauge, Area, DualAxes, Pie } from '@ant-design/plots';
import {
    SignalFilled,
    ThunderboltOutlined,
    CloudOutlined,
    FireOutlined,
    ReloadOutlined,
    DashboardOutlined,
    PlayCircleOutlined,
    PauseCircleOutlined,
    WifiOutlined,
    ExclamationCircleOutlined,
    BankOutlined,
    BarChartOutlined,
    PieChartOutlined,
    LineChartOutlined
} from '@ant-design/icons';
import { useWebSocketConfig } from '@/contexts/WebSocketConfigContext';
import {
    getQRSRPCmd, parseQRSRP, getQSINRCmd, parseQSINR, getQRSRQCmd, parseQRSRQ,
    getRealtimeRateCmd, parseRealtimeRate, getTrafficStatCmds, parseTrafficStat,
    getTempCmds, parseTemp, getSignedRateCmds, parseSignedRate, getOperatorCmd, parseOperator,
    getNetworkTypeCmd, parseNetworkType, getRegStatusCmd, parseRegStatus
} from '@/utils/atModule';
import { parseATISystemInfo } from '@/utils/atModule';
import { safeSendAT, ensureGroupReady, getCurrentGroupAsync } from '@/utils/atQueue';
import wsService from '@/services/websocket';
import { useModel } from '@umijs/max';
import { formatSpeed, formatBytes, getSignalColor, getSignalLevel } from '@/utils/formatUtils';
import { format } from 'path';
import { title } from 'process';
import { useScrollReset } from '@/hooks/useScrollReset';

// 添加样式来处理 InputNumber 的上下箭头背景
const inputNumberStyles = `
  .transparent-input-number .ant-input-number-handler-wrap {
    background: transparent !important;
  }
  .transparent-input-number .ant-input-number-handler-up {
    background: transparent !important;
    border: none !important;
  }
  .transparent-input-number .ant-input-number-handler-down {
    background: transparent !important;
    border: none !important;
  }
`;

// 注入样式
if (typeof document !== 'undefined') {
    const styleElement = document.createElement('style');
    styleElement.textContent = inputNumberStyles;
    document.head.appendChild(styleElement);
}

export default function Dashboard() {
    const { ip, port } = useWebSocketConfig();
    const { message } = App.useApp();
    const { token } = theme.useToken();

    // 使用滚动重置Hook
    useScrollReset();

    const [loading, setLoading] = useState(true);
    const [autoRefresh, setAutoRefresh] = useState(false);
    const [refreshInterval, setRefreshInterval] = useState(5);
    const intervalRef = useRef<any>(null);
    const unmountedRef = useRef(false); // 新增
    const { setInitialState } = useModel('@@initialState');

    // 数据状态
    const [signalData, setSignalData] = useState<any>(null);
    const [signalHistory, setSignalHistory] = useState<any[]>([]);
    const [speedData, setSpeedData] = useState<any>(null);
    const [speedHistory, setSpeedHistory] = useState<any[]>([]);
    const [trafficData, setTrafficData] = useState<any>(null);
    const [tempData, setTempData] = useState<any[]>([]);
    const [operator, setOperator] = useState<string>('');
    const [networkType, setNetworkType] = useState<string>('');
    const [regStatus, setRegStatus] = useState<number | undefined>(undefined);

    // 主动连接WebSocket并检测命令族
    useEffect(() => {
        if (ip && port && wsService.getStatus() !== 'open') {
            wsService.connect(`ws://${ip}:${port}`);
        }
        const setTitle = async () => {
            await ensureGroupReady(ip, port);
            const atiRaw = await safeSendAT('ATI');
            const { model } = parseATISystemInfo(atiRaw);
            setInitialState((prev: any) => ({
                ...prev,
                dynamicTitle: model ? `${model}` : 'Modem-Manager',
            }));
        };
        setTitle();
    }, [ip, port]);

    // 获取信号数据
    const fetchSignalData = async () => {
        if (unmountedRef.current) return;
        try {
            await ensureGroupReady(ip, port);
            if (unmountedRef.current) return;
            const realGroup = await getCurrentGroupAsync(ip, port);
            if (unmountedRef.current) return;

            const qrsrp = await safeSendAT(getQRSRPCmd(realGroup));
            const qsinr = await safeSendAT(getQSINRCmd(realGroup));
            const qrsrq = await safeSendAT(getQRSRQCmd(realGroup));
            if (unmountedRef.current) return;

            const rsrpResult = parseQRSRP(qrsrp, realGroup);
            const sinrResult = parseQSINR(qsinr, realGroup);
            const rsrqResult = parseQRSRQ(qrsrq, realGroup);

            const signalInfo = {
                rsrp: rsrpResult?.value,
                sinr: sinrResult?.value,
                rsrq: rsrqResult?.value,
                timestamp: new Date().getTime()
            };

            if (unmountedRef.current) return;
            setSignalData(signalInfo);
            setSignalHistory(prev => [...prev.slice(-29), signalInfo]); // 保留最近30个数据点
        } catch (error) {
            if (!unmountedRef.current) console.error('获取信号数据失败:', error);
        }
    };

    // 获取网速数据
    const fetchSpeedData = async () => {
        if (unmountedRef.current) return;
        try {
            await ensureGroupReady(ip, port);
            if (unmountedRef.current) return;
            const realGroup = await getCurrentGroupAsync(ip, port);
            if (unmountedRef.current) return;

            const rateCmd = getRealtimeRateCmd(realGroup);
            const rateRaw = await safeSendAT(rateCmd);
            if (unmountedRef.current) return;
            const rateInfo = parseRealtimeRate(rateRaw, realGroup);
            if (rateInfo) {
                const speedInfo = {
                    up: rateInfo.up,
                    down: rateInfo.down,
                    timestamp: new Date().getTime()
                };
                if (unmountedRef.current) return;
                setSpeedData(speedInfo);
                setSpeedHistory(prev => [...prev.slice(-29), speedInfo]); // 保留最近30个数据点
            }
        } catch (error) {
            if (!unmountedRef.current) console.error('获取网速数据失败:', error);
        }
    };

    // 获取流量数据
    const fetchTrafficData = async () => {
        if (unmountedRef.current) return;
        try {
            await ensureGroupReady(ip, port);
            if (unmountedRef.current) return;
            const realGroup = await getCurrentGroupAsync(ip, port);
            if (unmountedRef.current) return;

            const cmds = getTrafficStatCmds(realGroup);
            const results = [];
            for (const cmd of cmds) {
                results.push(await safeSendAT(cmd));
                if (unmountedRef.current) return;
            }
            const trafficInfo = parseTrafficStat(results, realGroup);
            if (unmountedRef.current) return;
            setTrafficData(trafficInfo);
        } catch (error) {
            if (!unmountedRef.current) console.error('获取流量数据失败:', error);
        }
    };

    // 获取温度数据
    const fetchTempData = async () => {
        if (unmountedRef.current) return;
        try {
            await ensureGroupReady(ip, port);
            if (unmountedRef.current) return;
            const realGroup = await getCurrentGroupAsync(ip, port);
            if (unmountedRef.current) return;

            const cmds = getTempCmds(realGroup);
            const results = [];
            for (const cmd of cmds) {
                results.push(await safeSendAT(cmd));
                if (unmountedRef.current) return;
            }
            const tempInfo = parseTemp(results, realGroup);
            if (unmountedRef.current) return;
            setTempData(tempInfo || []);
        } catch (error) {
            if (!unmountedRef.current) console.error('获取温度数据失败:', error);
        }
    };

    // 获取运营商信息
    const fetchOperatorInfo = async () => {
        if (unmountedRef.current) return;
        try {
            await ensureGroupReady(ip, port);
            if (unmountedRef.current) return;
            const realGroup = await getCurrentGroupAsync(ip, port);
            if (unmountedRef.current) return;

            const cmd = getOperatorCmd(realGroup);
            const raw = await safeSendAT(cmd);
            if (unmountedRef.current) return;
            const operatorInfo = parseOperator(raw, realGroup);
            if (unmountedRef.current) return;
            setOperator(operatorInfo || '');
        } catch (error) {
            if (!unmountedRef.current) console.error('获取运营商信息失败:', error);
        }
    };

    // 获取网络类型和注册状态
    const fetchNetworkInfo = async () => {
        if (unmountedRef.current) return;
        try {
            await ensureGroupReady(ip, port);
            if (unmountedRef.current) return;
            const realGroup = await getCurrentGroupAsync(ip, port);
            if (unmountedRef.current) return;

            const netTypeRaw = await safeSendAT(getNetworkTypeCmd(realGroup));
            if (unmountedRef.current) return;
            const regStatusCmds = getRegStatusCmd(realGroup);
            let regStatusRaw = undefined;
            for (const cmd of regStatusCmds) {
                const raw = await safeSendAT(cmd);
                if (unmountedRef.current) return;
                regStatusRaw = parseRegStatus(raw, realGroup);
                if (regStatusRaw) {
                    break;
                }
            }

            setNetworkType(parseNetworkType(netTypeRaw, realGroup) ?? '');
            setRegStatus(regStatusRaw);
        } catch (error) {
            if (!unmountedRef.current) console.error('获取网络信息失败:', error);
        }
    };

    // 刷新所有数据
    const cleanupAfterRefresh = () => {
        setSignalHistory(prev => prev.slice(-29));
        setSpeedHistory(prev => prev.slice(-29));
        // 这里可以根据需要添加更多清理逻辑
    };
    const refreshAllData = async () => {
        if (unmountedRef.current) return;
        setLoading(true);
        try {
            const fetchList = [
                fetchSignalData,
                fetchSpeedData,
                fetchTrafficData,
                fetchTempData,
                fetchOperatorInfo,
                fetchNetworkInfo
            ];
            for (const fetchFn of fetchList) {
                // eslint-disable-next-line no-await-in-loop
                await fetchFn();
                // 分帧，给主线程喘息机会
                // eslint-disable-next-line no-await-in-loop
                await new Promise(res => setTimeout(res, 0));
            }
        } catch (error) {
            if (!unmountedRef.current) message.error('数据刷新失败');
        }
        if (unmountedRef.current) return;
        setLoading(false);
        cleanupAfterRefresh();
    };

    // 自动刷新逻辑
    useEffect(() => {
        unmountedRef.current = false;
        if (!autoRefresh) {
            if (intervalRef.current) clearInterval(intervalRef.current);
            return;
        }
        intervalRef.current = setInterval(() => {
            refreshAllData();
        }, refreshInterval * 1000);
        return () => {
            if (intervalRef.current) clearInterval(intervalRef.current);
            intervalRef.current = null;
            unmountedRef.current = true;
            setLoading(false);
        };
    }, [autoRefresh, refreshInterval, ip, port]);

    // 初始加载
    useEffect(() => {
        unmountedRef.current = false;
        let cancel = false;
        const doQuery = () => {
            if (!cancel) {
                refreshAllData();
                setAutoRefresh(true);
            }
        };
        if (wsService.getStatus && wsService.getStatus() === 'open') {
            doQuery();
        } else {
            wsService.addOnOpenCallback(doQuery);
        }
        // 卸载时移除 onOpen 回调，防止回调堆积，并清空历史数据，异步分批清理
        return () => {
            cancel = true;
            unmountedRef.current = true;
            setAutoRefresh(false); // 提前关闭自动刷新
            wsService.clearAllATListeners();
            wsService.removeOnOpenCallback(doQuery);
        };
    }, [ip, port]);

    // 获取网络状态信息
    const getNetworkStatusInfo = () => {
        const regStatusMap: Record<number, { text: string, color: string, status: 'success' | 'warning' | 'error' }> = {
            0: { text: '未注册，未搜索', color: 'var(--ant-color-error)', status: 'error' },
            1: { text: '已注册，本地网络', color: 'var(--ant-color-success)', status: 'success' },
            2: { text: '未注册，正在搜索', color: 'var(--ant-color-warning)', status: 'warning' },
            3: { text: '注册被拒', color: 'var(--ant-color-error)', status: 'error' },
            4: { text: '未知（超出网络覆盖）', color: 'var(--ant-color-error)', status: 'error' },
            5: { text: '已注册，漫游网络', color: 'var(--ant-color-success)', status: 'success' },
            6: { text: '已注册SMS only本地网络', color: 'var(--ant-color-success)', status: 'success' },
            7: { text: '已注册SMS only漫游网络', color: 'var(--ant-color-success)', status: 'success' },
            8: { text: '仅用于紧急承载服务', color: 'var(--ant-color-warning)', status: 'warning' },
            9: { text: '注册CSFB不优先本地网络', color: 'var(--ant-color-warning)', status: 'warning' },
            10: { text: '注册CSFB不优先漫游网络', color: 'var(--ant-color-warning)', status: 'warning' },
        };
        return regStatusMap[regStatus ?? 0] || { text: '未知', color: token.colorText, status: 'warning' };
    };

    // 检测是否为暗色主题
    const isDarkTheme = token.colorBgContainer !== '#ffffff';

    // 获取图表主题配置
    const getChartTheme = () => ({ type: isDarkTheme ? 'dark' : 'light' });

    // 信号趋势图配置
    const signalChartData = signalHistory.map(item => [
        { time: new Date(item.timestamp).toLocaleTimeString(), value: item.rsrp, type: 'RSRP' },
        { time: new Date(item.timestamp).toLocaleTimeString(), value: item.sinr, type: 'SINR' },
        { time: new Date(item.timestamp).toLocaleTimeString(), value: item.rsrq, type: 'RSRQ' }
    ]).flat().filter(item => item && item.value !== undefined && item.value !== null);

    const signalTrendConfig = {
        theme: getChartTheme(),
        data: signalChartData,
        xField: 'time',
        yField: 'value',
        colorField: 'type',
        smooth: true,
        height: 360,
        shapeField: 'smooth',
        color: ['#1890ff', '#52c41a', '#fa8c16'],
        tooltip: { channel: 'y', valueFormatter: (value: number) => `${value} dBm` },
        areaStyle: {
            fillOpacity: 0.6,
        },
        line: {
            size: 1,
        },
        yAxis: {
            min: -80,
            max: 40,
        },
    };

    // 网速趋势图配置
    const speedChartData = speedHistory.map(item => [
        { time: new Date(item.timestamp).toLocaleTimeString(), value: item.up, type: '上行' },
        { time: new Date(item.timestamp).toLocaleTimeString(), value: item.down, type: '下行' }
    ]).flat().filter(item => item && item.value !== undefined && item.value !== null);

    const speedTrendConfig = {
        theme: getChartTheme(),
        data: speedChartData,
        xField: 'time',
        yField: 'value',
        colorField: 'type',
        smooth: true,
        height: 360,
        shapeField: 'smooth',
        color: [token.colorSuccess, token.colorPrimary],
        tooltip: { channel: 'y', valueFormatter: (value: number) => formatSpeed(value, true) },
        areaStyle: {
            fillOpacity: 0.6,
        },
        line: {
            size: 1,
        },
        // yAxis: {
        //     labelFormatter: (value: number) => formatSpeed(value),
        //     min: 0,
        //     label: {
        //         style: {
        //             fontSize: 12,
        //             formatter: (value: number) => formatSpeed(value),
        //         },
        //     },

        //     title: {
        //         text: '速率',
        //     },
        //     tickCount: 6,
        // },
        axis: {
            y: {
                labelFormatter: (value: number) => formatSpeed(value, true),
            },
        },
    };

    // 温度柱状图配置
    const filteredTempData = tempData.filter(item => item && item.value !== undefined && item.value !== null && item.value > 0);

    const tempChartConfig = {
        theme: getChartTheme(),
        data: filteredTempData,
        xField: 'name',
        yField: 'value',
        height: 360,
        style: {
            fill: (datum: any) => {
                const value = datum?.value;
                if (value === undefined || value === null) return '#d9d9d9'; // 灰色 - 无数据
                if (value >= 90) return '#ff4d4f'; // 红色 - 高温危险
                if (value >= 70) return '#fa8c16'; // 橙色 - 高温警告
                if (value >= 60) return '#faad14'; // 黄色 - 中等温度
                if (value >= 40) return '#52c41a'; // 绿色 - 正常温度
                return '#1890ff'; // 蓝色 - 低温
            },
        },
        // label: {
        //     position: 'top',
        //     style: {
        //         fontSize: 12,
        //         fontWeight: 'bold',
        //     },
        // },
        tooltip: {
            channel: 'y', valueFormatter: (value: number) => `${value}°C`
        },
        xAxis: {
            label: {
                autoRotate: true,
                autoHide: true,
                autoEllipsis: true,
                style: {
                    fontSize: 10,
                    textAlign: 'center',
                },
            },
        },
        yAxis: {
            min: 0,
            max: 110,
            title: {
                text: '温度 (°C)',
            },
        },
    };

    // 流量饼图配置
    const trafficChartData = [
        { type: '上传流量', value: trafficData?.up ?? 0 },
        { type: '下载流量', value: trafficData?.down ?? 0 }
    ];
    const trafficPieConfig = {
        theme: getChartTheme(),
        data: trafficChartData,
        angleField: 'value',
        colorField: 'type',
        radius: 1.0,
        height: 360,
        color: [token.colorSuccess, token.colorPrimary],
        label: {
            formatter: (datum: any, index: any) => {
                // 如果 datum 为空，使用 index（实际上是数据对象）
                const dataItem = datum || index;

                if (!dataItem || !dataItem.type) {
                    return '未知\n0 B';
                }

                // 如果值为0，不显示标签
                if (dataItem.value === 0) {
                    return '';
                }

                return `${dataItem.type}\n${formatBytes(dataItem.value || 0)}`;
            },
            style: {
                fontSize: 11,
                textAlign: 'center',
                fill: token.colorText,
                fontWeight: 500,
            },
            offset: 20,
        },
        tooltip: {
            title: (datum: any) => datum?.type || '未知',
            items: [{ channel: 'y', valueFormatter: (value: number) => formatBytes(value) }],
        },
    };

    const networkStatusInfo = getNetworkStatusInfo();

    return (
        <Card
            style={{ minHeight: 'calc(100vh - 48px)' }}
            className="my-card"
            title={
                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', flexWrap: 'wrap', gap: 16, padding: '0 8px' }}>
                    <div style={{ display: 'flex', alignItems: 'center', gap: 12, flexWrap: 'wrap', marginBottom: 0 }}>
                        <h2 style={{ marginRight: 16, marginBottom: 0 }}>
                            <DashboardOutlined style={{ marginRight: 8 }} />
                            网络监控仪表板
                        </h2>
                        <div style={{ display: 'flex', alignItems: 'center', gap: 2, flexWrap: 'wrap' }}>
                            {networkType && (
                                <Tag color="green" style={{ fontSize: 14, padding: '1px 6px' }}>{networkType}</Tag>
                            )}
                            <Tag color={networkStatusInfo.status === 'success' ? 'success' : networkStatusInfo.status === 'warning' ? 'warning' : 'error'} style={{ fontSize: 14, padding: '1px 6px' }}>{networkStatusInfo.text}</Tag>
                        </div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', gap: 8, flexWrap: 'wrap', marginTop: 0, padding: '8px 0' }}>
                        <Button
                            type="default"
                            className="my-btn"
                            icon={<ReloadOutlined />}
                            onClick={refreshAllData}
                            loading={loading}
                        >
                            刷新
                        </Button>
                        {!autoRefresh ? (
                            <Button
                                type="default"
                                style={{ borderRadius: 6, padding: '0 18px', height: 32, fontSize: 14, fontWeight: 500, marginLeft: 8, borderColor: 'var(--ant-color-primary)', color: 'var(--ant-color-primary)' }}
                                onClick={() => setAutoRefresh(true)}
                            >
                                自动刷新
                            </Button>
                        ) : (
                            <button
                                style={{
                                    display: 'inline-flex',
                                    alignItems: 'center',
                                    border: '1px solid var(--ant-color-primary)',
                                    borderRadius: 6,
                                    padding: '0 12px',
                                    height: 32,
                                    background: 'var(--ant-color-info-bg)',
                                    boxSizing: 'border-box',
                                    userSelect: 'none',
                                    color: 'var(--ant-color-primary)',
                                    fontWeight: 500,
                                    fontSize: 14,
                                    outline: 'none',
                                    transition: 'background 0.2s, border 0.2s',
                                }}
                                type="button"
                            >
                                <span
                                    style={{ marginRight: 0, cursor: 'pointer' }}
                                    onClick={e => { e.stopPropagation(); setAutoRefresh(false); }}
                                >
                                    自动刷新
                                </span>
                                <InputNumber
                                    min={2}
                                    max={60}
                                    value={refreshInterval}
                                    onChange={v => setRefreshInterval(Number(v) || 5)}
                                    onKeyDown={e => {
                                        if (!['ArrowUp', 'ArrowDown', 'Tab', 'Shift', 'Control', 'Alt'].includes(e.key)) {
                                            e.preventDefault();
                                        }
                                    }}
                                    style={{
                                        width: 52,
                                        height: '100%',
                                        border: 'none',
                                        background: 'transparent',
                                        color: 'var(--ant-color-primary)',
                                        fontSize: 14,
                                        textAlign: 'left',
                                        margin: '0px',
                                        outline: 'none',
                                        boxShadow: 'none',
                                        lineHeight: '24px',
                                        padding: 0,
                                    }}
                                    className="transparent-input-number"
                                />
                                <span style={{ marginLeft: 2, lineHeight: '24px' }} onClick={e => { e.stopPropagation(); setAutoRefresh(false); }}>秒</span>
                            </button>
                        )}
                    </div>
                </div>
            }
        >

            {/* 网络状态警告 */}
            {/* {regStatus !== 1 && regStatus !== 5 && regStatus !== 6 && regStatus !== 7 && (
                <Alert
                    message="网络连接异常"
                    description={networkStatusInfo.text}
                    type={networkStatusInfo.status}
                    showIcon
                    style={{ marginBottom: 16, marginTop: 0 }}
                />
            )} */}

            {/* 信号质量卡片 */}
            <Row gutter={[16, 16]} style={{ marginBottom: 16 }}>
                <Col xs={24} lg={8}>
                    <Card
                        title={
                            <span>
                                <SignalFilled style={{ marginRight: 8, color: token.colorPrimary }} />
                                信号质量
                            </span>
                        }
                        style={{ height: 220 }}
                        className="my-inline-card"
                    >
                        <div style={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            alignItems: 'center',
                            height: '100%',
                            padding: '10px 0'
                        }}>
                            <div style={{
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                flex: 1
                            }}>
                                <Progress
                                    type="circle"
                                    percent={signalData?.rsrp ? Math.max(0, Math.min(100, Math.round((signalData.rsrp + 120) * 100 / 40))) : 0}
                                    format={percent => (
                                        <div style={{ textAlign: 'center' }}>
                                            <div style={{
                                                fontSize: 40,
                                                fontWeight: 'bold',
                                                color: getSignalColor(signalData?.rsrp, 'rsrp'),
                                                lineHeight: 1.2,
                                                marginBottom: 4
                                            }}>
                                                {signalData?.rsrp || '-'}
                                            </div>
                                            <div style={{
                                                fontSize: 14,
                                                color: token.colorTextSecondary,
                                            }}>
                                                dBm
                                            </div>
                                        </div>
                                    )}
                                    strokeColor={getSignalColor(signalData?.rsrp, 'rsrp')}
                                    size={140}
                                    strokeWidth={8}
                                />
                            </div>
                            {operator && (
                                <div style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    padding: '0 16px',
                                    borderLeft: `1px solid ${token.colorBorderSecondary}`,
                                    height: '100%',
                                    minWidth: '40%'
                                }}>
                                    {/* 运营商图标 */}
                                    {operator === '中国移动' && <img src="/icons/china_mobile.png" alt="中国移动" style={{ width: 64, height: 64, marginBottom: 8 }} />}
                                    {operator === '中国联通' && <img src="/icons/china_unicom.png" alt="中国联通" style={{ width: 64, height: 64, marginBottom: 8 }} />}
                                    {operator === '中国电信' && <img src="/icons/china_telecom.png" alt="中国电信" style={{ width: 64, height: 64, marginBottom: 8 }} />}
                                    {operator === '中国广电' && <img src="/icons/china_broadcast.png" alt="中国广电" style={{ width: 64, height: 64, marginBottom: 8 }} />}
                                    <div style={{
                                        fontSize: 16,
                                        color: token.colorTextSecondary,
                                        textAlign: 'center',
                                        lineHeight: 1.2
                                    }}>
                                        {operator}
                                    </div>
                                </div>
                            )}
                        </div>
                    </Card>
                </Col>
                <Col xs={24} lg={8}>
                    <Card
                        title={
                            <span>
                                <ThunderboltOutlined style={{ marginRight: 8, color: token.colorSuccess }} />
                                实时网速
                            </span>
                        }
                        className="my-inline-card"
                        style={{ height: 220 }}
                    >
                        <div
                            style={{
                                width: '100%',
                                height: 156,
                                display: 'flex',
                                alignItems: 'stretch',
                                justifyContent: 'center',
                                padding: '6px 6px',
                                boxSizing: 'border-box',
                            }}
                        >
                            <div style={{
                                display: 'flex',
                                flexDirection: 'row',
                                justifyContent: 'space-between',
                                alignItems: 'stretch',
                                width: '100%',
                                gap: 16,
                                height: '100%',
                            }}>
                                {/* 上行速率小卡片 */}
                                <div style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    borderRadius: 8,
                                    height: '100%',
                                    background: 'var(--ant-color-fill-tertiary)',
                                    padding: '16px 16px',
                                    boxSizing: 'border-box',
                                    boxShadow: '0 1px 4px rgba(0,0,0,0.04)',
                                }}>
                                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 24 }}>
                                        <span style={{ fontSize: 18, fontWeight: 600, color: token.colorText }}>上行速率</span>
                                        <img src="/icons/up_speed.svg" alt="上行速率" style={{ width: 36, height: 36, marginLeft: 12 }} />
                                    </div>
                                    <div style={{ display: 'flex', alignItems: 'baseline', justifyContent: 'flex-start', marginTop: 2 }}>
                                        <span style={{ fontSize: 32, fontWeight: 700, color: token.colorSuccess, lineHeight: 1 }}>
                                            {formatSpeed(speedData?.up || 0, true).split(' ')[0]}
                                        </span>
                                        <span style={{ fontSize: 20, fontWeight: 400, color: token.colorText, marginLeft: 8 }}>
                                            {formatSpeed(speedData?.up || 0, true).split(' ')[1] || 'B/s'}
                                        </span>
                                    </div>
                                </div>
                                {/* 下行速率小卡片 */}
                                <div style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    background: 'var(--ant-color-fill-tertiary)',
                                    borderRadius: 8,
                                    height: '100%',
                                    padding: '16px 16px',
                                    boxSizing: 'border-box',
                                    boxShadow: '0 1px 4px rgba(0,0,0,0.04)',
                                }}>
                                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 24 }}>
                                        <span style={{ fontSize: 18, fontWeight: 600, color: token.colorText }}>下行速率</span>
                                        <img src="/icons/down_speed.svg" alt="下行速率" style={{ width: 36, height: 36, marginLeft: 12 }} />
                                    </div>
                                    <div style={{ display: 'flex', alignItems: 'baseline', justifyContent: 'flex-start', marginTop: 2 }}>
                                        <span style={{ fontSize: 32, fontWeight: 700, color: token.colorPrimary, lineHeight: 1 }}>
                                            {formatSpeed(speedData?.down || 0, true).split(' ')[0]}
                                        </span>
                                        <span style={{ fontSize: 20, fontWeight: 400, color: token.colorText, marginLeft: 8 }}>
                                            {formatSpeed(speedData?.down || 0, true).split(' ')[1] || 'B/s'}
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Card>
                </Col>
                <Col xs={24} lg={8}>
                    <Card
                        title={
                            <span>
                                <CloudOutlined style={{ marginRight: 8, color: token.colorWarning }} />
                                流量统计
                            </span>
                        }
                        className="my-inline-card"
                        style={{ height: 220 }}
                    >
                        <div
                            style={{
                                width: '100%',
                                height: 156,
                                display: 'flex',
                                alignItems: 'stretch',
                                justifyContent: 'center',
                                padding: '6px 6px',
                                boxSizing: 'border-box',
                            }}
                        >
                            <div style={{
                                display: 'flex',
                                flexDirection: 'row',
                                justifyContent: 'space-between',
                                alignItems: 'stretch',
                                width: '100%',
                                gap: 16,
                                height: '100%',
                            }}>
                                {/* 上传流量分区 */}
                                <div style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    background: 'var(--ant-color-fill-tertiary)',
                                    borderRadius: 8,
                                    height: '100%',
                                    padding: '16px 16px',
                                    boxSizing: 'border-box',
                                    boxShadow: '0 1px 4px rgba(0,0,0,0.04)',
                                }}>
                                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 24 }}>
                                        <span style={{ fontSize: 18, fontWeight: 600, color: token.colorText }}>上传流量</span>
                                        <img src="/icons/up.svg" alt="上传流量" style={{ width: 36, height: 36, marginLeft: 12 }} />
                                    </div>
                                    <div style={{ display: 'flex', alignItems: 'baseline', justifyContent: 'flex-start', marginTop: 2 }}>
                                        <span style={{ fontSize: 32, fontWeight: 700, color: token.colorSuccess, lineHeight: 1 }}>
                                            {formatBytes(trafficData?.up || 0).split(' ')[0]}
                                        </span>
                                        <span style={{ fontSize: 20, fontWeight: 400, color: token.colorText, marginLeft: 8 }}>
                                            {formatBytes(trafficData?.up || 0).split(' ')[1] || 'B'}
                                        </span>
                                    </div>
                                </div>
                                {/* 下载流量分区 */}
                                <div style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    justifyContent: 'center',
                                    background: 'var(--ant-color-fill-tertiary)',
                                    borderRadius: 8,
                                    height: '100%',
                                    padding: '16px 16px',
                                    boxSizing: 'border-box',
                                    boxShadow: '0 1px 4px rgba(0,0,0,0.04)',
                                }}>
                                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 24 }}>
                                        <span style={{ fontSize: 18, fontWeight: 600, color: token.colorText }}>下载流量</span>
                                        <img src="/icons/down.svg" alt="下载流量" style={{ width: 36, height: 36, marginLeft: 12 }} />
                                    </div>
                                    <div style={{ display: 'flex', alignItems: 'baseline', justifyContent: 'flex-start', marginTop: 2 }}>
                                        <span style={{ fontSize: 32, fontWeight: 700, color: token.colorPrimary, lineHeight: 1 }}>
                                            {formatBytes(trafficData?.down || 0).split(' ')[0]}
                                        </span>
                                        <span style={{ fontSize: 20, fontWeight: 400, color: token.colorText, marginLeft: 8 }}>
                                            {formatBytes(trafficData?.down || 0).split(' ')[1] || 'B'}
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Card>
                </Col>
            </Row>

            {/* 信号参数详情 */}
            <Row gutter={[16, 16]} style={{ marginBottom: 16 }}>
                <Col xs={24} sm={6}>
                    <Card>
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            height: '100%'
                        }}>
                            <div style={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'flex-start',
                                flex: 1
                            }}>
                                <div style={{ flex: 1 }}>
                                    <div style={{
                                        fontSize: 18,
                                        fontWeight: 500,
                                        color: token.colorText,
                                        marginBottom: 4
                                    }}>
                                        RSRP
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary,
                                        marginBottom: 12
                                    }}>
                                        参考信号接收功率
                                    </div>
                                </div>
                                <div style={{
                                    textAlign: 'right',
                                    flex: 1
                                }}>
                                    <div style={{
                                        fontSize: 32,
                                        fontWeight: 'bold',
                                        color: getSignalColor(signalData?.rsrp, 'rsrp'),
                                        lineHeight: 1.2
                                    }}>
                                        {signalData?.rsrp || '-'}
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary
                                    }}>
                                        dBm
                                    </div>
                                </div>
                            </div>
                            <Progress
                                percent={signalData?.rsrp ? Math.max(0, Math.min(100, Math.round((signalData.rsrp + 157) * 100 / 125))) : 0}
                                strokeColor={getSignalColor(signalData?.rsrp, 'rsrp')}
                                showInfo={false}
                                size="small"
                                style={{ marginBottom: 0 }}
                                className="full-width-progress"
                            />
                        </div>
                    </Card>
                </Col>
                <Col xs={24} sm={6}>
                    <Card>
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            height: '100%'
                        }}>
                            <div style={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'flex-start',
                                flex: 1
                            }}>
                                <div style={{ flex: 1 }}>
                                    <div style={{
                                        fontSize: 18,
                                        fontWeight: 500,
                                        color: token.colorText,
                                        marginBottom: 4
                                    }}>
                                        SINR
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary,
                                        marginBottom: 12
                                    }}>
                                        信噪比
                                    </div>
                                </div>
                                <div style={{
                                    textAlign: 'right',
                                    flex: 1
                                }}>
                                    <div style={{
                                        fontSize: 32,
                                        fontWeight: 'bold',
                                        color: getSignalColor(signalData?.sinr, 'sinr'),
                                        lineHeight: 1.2
                                    }}>
                                        {signalData?.sinr || '-'}
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary
                                    }}>
                                        dB
                                    </div>
                                </div>
                            </div>
                            <Progress
                                percent={signalData?.sinr ? Math.max(0, Math.min(100, Math.round((signalData.sinr + 24) * 100 / 64))) : 0}
                                strokeColor={getSignalColor(signalData?.sinr, 'sinr')}
                                showInfo={false}
                                size="small"
                                style={{ marginBottom: 0 }}
                                className="full-width-progress"
                            />
                        </div>
                    </Card>
                </Col>
                <Col xs={24} sm={6}>
                    <Card>
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            height: '100%'
                        }}>
                            <div style={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'flex-start',
                                flex: 1
                            }}>
                                <div style={{ flex: 1 }}>
                                    <div style={{
                                        fontSize: 18,
                                        fontWeight: 500,
                                        color: token.colorText,
                                        marginBottom: 4
                                    }}>
                                        RSRQ
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary,
                                        marginBottom: 12
                                    }}>
                                        参考信号接收质量
                                    </div>
                                </div>
                                <div style={{
                                    textAlign: 'right',
                                    flex: 1
                                }}>
                                    <div style={{
                                        fontSize: 32,
                                        fontWeight: 'bold',
                                        color: getSignalColor(signalData?.rsrq, 'rsrq'),
                                        lineHeight: 1.2
                                    }}>
                                        {signalData?.rsrq || '-'}
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary
                                    }}>
                                        dB
                                    </div>
                                </div>
                            </div>
                            <Progress
                                percent={signalData?.rsrq ? Math.max(0, Math.min(100, Math.round((signalData.rsrq + 44) * 100 / 64))) : 0}
                                strokeColor={getSignalColor(signalData?.rsrq, 'rsrq')}
                                showInfo={false}
                                size="small"
                                style={{ marginBottom: 0 }}
                                className="full-width-progress"
                            />
                        </div>
                    </Card>
                </Col>
                <Col xs={24} sm={6}>
                    <Card>
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            height: '100%'
                        }}>
                            <div style={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                alignItems: 'flex-start',
                                flex: 1
                            }}>
                                <div style={{ flex: 1 }}>
                                    <div style={{
                                        fontSize: 18,
                                        fontWeight: 500,
                                        color: token.colorText,
                                        marginBottom: 4
                                    }}>
                                        信号等级
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary,
                                        marginBottom: 12
                                    }}>
                                        信号强度等级
                                    </div>
                                </div>
                                <div style={{
                                    textAlign: 'right',
                                    flex: 1
                                }}>
                                    <div style={{
                                        fontSize: 32,
                                        fontWeight: 'bold',
                                        color: getSignalColor(signalData?.rsrp, 'rsrp'),
                                        lineHeight: 1.2
                                    }}>
                                        {getSignalLevel(signalData?.rsrp, 'rsrp')}/4
                                    </div>
                                    <div style={{
                                        fontSize: 12,
                                        color: token.colorTextSecondary
                                    }}>
                                        等级
                                    </div>
                                </div>
                            </div>
                            <Progress
                                percent={getSignalLevel(signalData?.rsrp, 'rsrp') * 25}
                                strokeColor={getSignalColor(signalData?.rsrp, 'rsrp')}
                                showInfo={false}
                                size="small"
                                style={{ marginBottom: 0 }}
                                className="full-width-progress"
                            />
                        </div>
                    </Card>
                </Col>
            </Row>

            {/* 图表区域 */}
            <Row gutter={[16, 16]} style={{ marginBottom: 16 }}>
                <Col xs={24} lg={12}>
                    <Card
                        title={
                            <span>
                                <BarChartOutlined style={{ marginRight: 8, color: token.colorPrimary }} />
                                网速趋势监控
                            </span>
                        }
                        className="my-inline-card"
                        style={{}}
                        extra={
                            <span
                                style={{
                                    background: 'var(--ant-color-fill-tertiary)',
                                    borderRadius: 2,
                                    padding: '0 8px',
                                    height: 24,
                                    display: 'inline-flex',
                                    alignItems: 'center',
                                    marginLeft: 12,
                                    fontSize: 12,
                                    color: 'var(--ant-color-text-secondary)',
                                    fontWeight: 'normal',
                                    lineHeight: '32px',
                                }}
                            >
                                实时监控网络速率变化
                            </span>
                        }
                    >
                        {speedHistory.length > 0 ? (
                            <Area {...speedTrendConfig} />
                        ) : (
                            <div style={{ height: 360, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                                    <img src="/icons/zanwuxinxi.svg" alt="暂无信息" style={{ width: 128, height: 128, marginBottom: 8, opacity: 0.6 }} />
                                    <div style={{ fontSize: 14, color: token.colorTextSecondary }}>暂无网速趋势数据</div>
                                </div>
                            </div>
                        )}
                    </Card>
                </Col>
                <Col xs={24} lg={12}>
                    <Card
                        title={
                            <span>
                                <PieChartOutlined style={{ marginRight: 8, color: token.colorPrimary }} />
                                流量分布
                            </span>
                        }
                        className="my-inline-card"
                        style={{}}
                        extra={
                            <span
                                style={{
                                    background: 'var(--ant-color-fill-tertiary)',
                                    borderRadius: 2,
                                    padding: '0 8px',
                                    height: 24,
                                    display: 'inline-flex',
                                    alignItems: 'center',
                                    marginLeft: 12,
                                    fontSize: 12,
                                    color: 'var(--ant-color-text-secondary)',
                                    fontWeight: 'normal',
                                    lineHeight: '32px',
                                }}
                            >
                                上传与下载流量占比
                            </span>
                        }
                    >
                        {trafficData ? (
                            <Pie {...trafficPieConfig} />
                        ) : (
                            <div style={{ height: 360, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                                    <img src="/icons/zanwuxinxi.svg" alt="暂无信息" style={{ width: 128, height: 128, marginBottom: 8, opacity: 0.6 }} />
                                    <div style={{ fontSize: 14, color: token.colorTextSecondary }}>暂无流量数据</div>
                                </div>
                            </div>
                        )}
                    </Card>
                </Col>
            </Row>

            {/* 流量和温度监控 */}
            <Row gutter={[16, 16]}>
                <Col xs={24} lg={12}>
                    <Card
                        title={
                            <span>
                                <LineChartOutlined style={{ marginRight: 8, color: token.colorPrimary }} />
                                信号趋势监控
                            </span>
                        }
                        className="my-inline-card"
                        style={{}}
                        extra={
                            <span
                                style={{
                                    background: 'var(--ant-color-fill-tertiary)',
                                    borderRadius: 2,
                                    padding: '0 8px',
                                    height: 24,
                                    display: 'inline-flex',
                                    alignItems: 'center',
                                    marginLeft: 12,
                                    fontSize: 12,
                                    color: 'var(--ant-color-text-secondary)',
                                    fontWeight: 'normal',
                                    lineHeight: '32px',
                                }}
                            >
                                实时监控信号参数变化
                            </span>
                        }
                    >
                        {signalHistory.length > 0 ? (
                            <Area {...signalTrendConfig} />
                        ) : (
                            <div style={{ height: 360, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                                    <img src="/icons/zanwuxinxi.svg" alt="暂无信息" style={{ width: 128, height: 128, marginBottom: 8, opacity: 0.6 }} />
                                    <div style={{ fontSize: 14, color: token.colorTextSecondary }}>暂无信号趋势数据</div>
                                </div>
                            </div>
                        )}
                    </Card>
                </Col>
                <Col xs={24} lg={12}>
                    <Card
                        className="my-inline-card"
                        title={
                            <span>
                                <FireOutlined style={{ marginRight: 8, color: token.colorError }} />
                                模组温度监控
                            </span>
                        }
                        style={{}}
                        extra={
                            <span
                                style={{
                                    background: 'var(--ant-color-fill-tertiary)',
                                    borderRadius: 2,
                                    padding: '0 8px',
                                    height: 24,
                                    display: 'inline-flex',
                                    alignItems: 'center',
                                    marginLeft: 12,
                                    fontSize: 12,
                                    color: 'var(--ant-color-text-secondary)',
                                    fontWeight: 'normal',
                                    lineHeight: '32px',
                                }}
                            >
                                监控各功能模块温度状态
                            </span>
                        }
                    >
                        {filteredTempData.length > 0 ? (
                            <Column {...tempChartConfig} />
                        ) : (
                            <div style={{ height: 360, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                                    <img src="/icons/zanwuxinxi.svg" alt="暂无信息" style={{ width: 128, height: 128, marginBottom: 8, opacity: 0.6 }} />
                                    <div style={{ fontSize: 14, color: token.colorTextSecondary }}>暂无温度数据</div>
                                </div>
                            </div>
                        )}
                    </Card>
                </Col>
            </Row>
        </Card>
    );
}
